@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  src: url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}

body {
  background-color: #ffffff;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Lato", "Arial", sans-serif;
  color: #444444;
  display: flex;
  flex-direction: column;
}

/*
* {
  outline: 1px solid #000000;
}
*/
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-style: normal;
  box-sizing: border-box;
}

.main-container,
.form,
.catalog-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-weight: 500;
  margin: 0;
}
@media (min-width: 768px) {
  .main-header {
    margin-bottom: 62px;
  }
}
@media (min-width: 1440px) {
  .main-header {
    margin-bottom: 32px;
  }
}

@media (min-width: 1440px) {
  .main-header--bg::before {
    content: "";
    position: absolute;
    display: block;
    width: 50%;
    min-height: 694px;
    top: 0;
    left: 50%;
    background: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat/index-bg-cat-desktop.jpg") no-repeat;
    background-size: cover;
    z-index: -1;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-header--bg::before {
    background: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat/index-bg-cat-desktop@2x.jpg");
    background-size: cover;
  }
}

.main-header__header {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-header__header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 708px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .main-header__header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 1220px;
    margin: 0 auto;
    height: 107px;
  }
}

.main-header-wrapper {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .main-header-wrapper {
    max-height: 52px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.main-header__container {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin: 0 auto;
  justify-content: center;
}

.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:focus {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}
@media (min-width: 1440px) {
  .main-header__logo {
    width: 202px;
    height: 59px;
  }
}

.main-header__logo-container {
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 38px;
  padding: 10px 20px 17px;
}
@media (min-width: 768px) {
  .main-header__logo-container {
    padding: 0;
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo-container {
    width: 202px;
    height: 59px;
    margin-top: 53px;
  }
}

.main-header__toggle {
  border: none;
  padding: 0;
  height: 38px;
  width: 28px;
  position: right;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  right: 19px;
}

.main-header--opened .main-header__toggle {
  top: 0;
  right: auto;
  left: 0;
}
.main-header--opened .main-header__toggle::before, .main-header--opened .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 20px;
  width: 28px;
  height: 2px;
  background-color: #000000;
}
.main-header--opened .main-header__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-header--opened .main-header__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-header--closed .main-nav {
    display: none;
  }
}

@media (max-width: 768px) {
  .main-header--opened .main-nav {
    position: absolute;
    top: 65px;
    left: 0;
    background-color: #ffffff;
    z-index: 2;
    transition: 0.5s linear;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .main-header--opened .main-nav {
    width: 440px;
  }
}

.main-header__toggle--no-js {
  display: none;
}

.main-header--nojs .main-nav {
  display: block;
}

.logo-text_img {
  width: 101px;
  height: 18px;
}
.logo-text_img:hover {
  opacity: 0.3;
}
.logo-text_img:focus-visible {
  opacity: 0.3;
}
.logo-text_img:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .logo-text_img {
    display: none;
  }
}
@media (min-width: 1440px) {
  .logo-text_img {
    display: none;
  }
}

.main-nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
  min-width: 320px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    min-width: 441px;
    gap: 33px;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    gap: 35px;
    max-width: 1000px;
  }
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#000000, 30%);
  box-shadow: 0 8px 0 0 fade(#000000, 30%), 0 16px 0 0 fade(#000000, 30%);
}

.main-nav--no-js.main-nav {
  display: block;
  position: relative;
}

@media (min-width: 1440px) {
  .main-nav__wrapper {
    padding-top: 69px;
  }
}

.main-nav__item {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding: 24px 0 19px;
  text-align: center;
  max-height: 65px;
}
.main-nav__item:nth-child(1) {
  border-top: 1px solid #e6e6e6;
}
.main-nav__item:nth-child(2) {
  border-top: 1px solid #e6e6e6;
}
.main-nav__item:nth-child(3) {
  border-top: 1px solid #e6e6e6;
  border-bottom: 1px solid #e6e6e6;
}

.main-nav__link {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
  font-family: "Oswald", "Roboto", sans-serif;
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  color: #000000;
  transition: 0.3s all;
}
.main-nav__link:hover {
  transition: 0.3s all;
  opacity: 0.6;
}
.main-nav__link:focus {
  transition: 0.3s all;
  opacity: 0.6;
}
.main-nav__link:active {
  transition: 0.3s all;
  opacity: 0.3;
}

.main-nav__link--current-link {
  padding-bottom: 10px;
  border-bottom: 2px solid #68b738;
}

@media (min-width: 768px) {
  .main-nav {
    z-index: 1;
  }

  .main-nav__list {
    max-width: 470px;
  }

  .main-nav__link--current-link {
    padding-bottom: 8px;
    border-bottom: 2px solid #68b738;
  }

  .main-nav__link--current-link-catalog {
    padding-bottom: 3px;
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-nav__link--current-link {
    padding-bottom: 8px;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    padding: 0;
    padding-top: 18px;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .main-nav__item {
    padding-top: 0;
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: #000000;
  }
}
@media (min-width: 768px) {
  .main-nav__item:nth-child(1),
.main-nav__item:nth-child(2),
.main-nav__item:nth-child(3) {
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav--desk {
    z-index: 1;
  }

  .main-nav__list.main-nav__list--desk {
    max-width: 550px;
  }

  .main-nav {
    z-index: 1;
  }

  .main-nav__list {
    max-width: 890px;
  }

  .main-nav__link {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    color: #000000;
  }

  .main-nav__link--current-link-catalog {
    padding-bottom: 8px;
    border-bottom: 2px solid #68b738;
  }

  .main-nav__item:nth-child(1),
.main-nav__item:nth-child(2),
.main-nav__item:nth-child(3) {
    border: none;
  }

  .main-nav__link--white-link {
    border-bottom: 2px solid #ffffff;
  }

  .main-nav__link--white-text {
    color: #ffffff;
  }
}
