.radio-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  row-gap: 23px;
  padding: 30px 0 22px 0;
}

.radio-list__field {
  position: relative;
  cursor: pointer;
}

.radio-list__control {
  margin-right: 15px;
  cursor: pointer;
}

.radio-list__control[type=radio]:checked + .radio-list__mark::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
  width: 13px;
  height: 13px;
  background-color: #68b738;
  border-radius: 50%;
}
.radio-list__control[type=radio]:checked + .radio-list__mark::before:hover {
  background-color: #68b738;
}
.radio-list__control[type=radio]:checked + .radio-list__mark::before:disabled {
  background-color: #68b738;
  opacity: 0.3;
}

.radio-list__control:hover .radio-list__mark {
  border-color: 1px solid #bababa;
}

.radio-list__control:focus-visible + .radio-list__mark {
  border-color: 1px solid #000000;
}

.radio-list__control:disabled + .radio-list__mark {
  border-color: 1px solid #bababa;
}

.radio-list__mark {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 28px;
  height: 28px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
